import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";


import Services from './Services'
import Signup from './signup/Signup'
import Signin from './signin/Signin'


function App(){
    return (
        <Router>
            <Switch>
                <Route path="/services">
                    <Services />
                </Route>
                <Route path="/signin">
                    <Signin />
                </Route>
                <Route path="/">
                    <Signup />
                </Route>                
            </Switch>
        </Router>
    );
}

export default App